<template>
  <view>
    <view class="list" v-for="(list,index) in chapterList" :key="index">
      <text @click="intoReadBook(list.chapterId,index)">{{list.title}}</text>
    </view>
  </view>
</template>

<script>
  export default {
    data() {
      return {
        id:'',
        chapterList:[]
      };
    },
    onLoad(options) {
      this.id = options.id;
      //获取目录
      this.chapterList = JSON.parse(uni.getStorageSync(this.id+'-chapterList')||'[]')
    },
    methods:{
      //进入章节
      intoReadBook(id,index){
        uni.navigateTo({
          url:`/subpag/readBook/readBook?id=${this.id}&chapterId=${id}&index=${index}`
        })
      }
    }
  }
</script>

<style lang="scss">
.list{
  display: flex;
  flex-direction: column;
  text{
    border-bottom: 2rpx solid #efefef;
    margin-bottom:20rpx;
    height: 40rpx;
    line-height: 40rpx;
  }
}
</style>
